___CSS_LOADER_EXPORT___.push([module.id, `/* Main Footer Styles */
.footer {
  background-color: var(--footer-bg-color, #1f1f1f); /* Default background color */
  color: var(--footer-text-color, #ffffff); /* Default text color */
  padding: 40px 20px 15px;
  font-family: 'Arial', sans-serif;
  text-align: center;
}

/* Footer Grid Layout */
.footer-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  max-width: 1200px;
  margin: 0 auto;
  justify-items: center;
}

/* Footer Section */
.footer-section {
  text-align: center;
}

.footer-section h4 {
  font-size: 18px;
  margin-bottom: 15px;
  color: var(--footer-section-title-color, #ffdd57); /* Default title color */
}

.footer-link {
  text-decoration: none;
  font-size: 14px;
  margin-bottom: 10px;
  display: block;
  color: inherit;
  transition: color 0.3s ease;
}

.footer-link:hover {
  color: var(--footer-link-hover-color, #ffa726); /* Link hover color */
}

/* Footer Social Links */
.footer-social {
  display: flex;
  gap: 15px;
  margin-top: 15px;
  justify-content: center;
}

.footer-social-icon {
  font-size: 20px;
  color: inherit;
  transition: color 0.3s ease;
}

.footer-social-icon:hover {
  color: var(--footer-social-hover-color, #ffdd57); /* Hover color for social icons */
}

/* Logo Styling */
.footer-logo {
  max-width: 150px;
  margin-bottom: 15px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* Footer Bottom */
.footer-bottom {
  margin-top: 30px;
  text-align: center;
  font-size: 14px;
  border-top: 1px solid #444;
  color: var(--footer-bottom-color, #bbb);
}

/* Responsive Design */
@media (max-width: 768px) {
  .footer-grid {
    gap: 20px;
  }

  .footer-bottom {
    font-size: 12px;
  }
}

@media (max-width: 480px) {
  .footer-section h4 {
    font-size: 16px;
  }

  .footer-link,
  .footer-address p {
    font-size: 12px;
  }
}
